  position: absolute;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.87);
  font: 16px Roboto, arial, sans-serif;
  height: auto;
  padding: 24px;
  text-align: left;
  top: 244.5px;
  left: 628px;
  right: 628px;
}
.firebaseui-list-box-dialog {
  display: block;
  visibility: visible;
}
.mdl-dialog,
.firebaseui-dialog,
