// }}
@media (min-width: 320px) and (max-width: 960px) {
  .v-dialog__content .dialogcss,
  .v-dialog {
    margin: 5px;
  }
  .dialogcss {
    max-height: 60% !important;
  }
}
@media (max-height: 450px) {
  .g-recaptcha-bubble-arrow + div:not(.g-recaptcha-bubble-arrow) {
    overflow-y: scroll;
    height: 80vh !important;
  }
}
</style>
