export const downloadUrl = async (fileName) => {
  if (
    store.state.currentForm.id === "default_form" ||
    store.state.currentForm.id === "" ||
    store.state.currentForm.id === "undefined"
  ) {
    const currentUrl = window.location.href;
    var stuff = currentUrl.split("/");
    await generateJWTToken(stuff[4]);
  } else {
    await generateJWTToken(store.state.currentForm.id);
  }
  return fetch(
    `${process.env.VUE_APP_API_ENDPOINT}fsdata/v1/get-download-url`,
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: getJWTToken() || "",
      },
      body: JSON.stringify({ name: fileName }),
    }
  );
};

export const updateFile = (fileName, content) => {
  return fetch(`${process.env.VUE_APP_API_ENDPOINT}fsdata/v1/update-file`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: getJWTToken() || "",
    },
    body: JSON.stringify({ name: fileName, data: content }),
  });
};

export const save = async (state, country, phone, otp) => {
  var Order_response = {};
  var Order_details = {};
  var Items = [];
  Order_details["merchant_id"] = state.id;
  Order_details["order_form"] = state.shopname;
  Order_details["order_status"] = "Pending";
  Order_details["total_amount"] = parseFloat(state.amount).toFixed(2);
  Order_details["currency"] = state.currency;
  Order_details["symbol"] = state.symbol;
  Order_details["created"] = Date.now();
  state.catalogue.map((item) => {
    if (item.type == "item") {
      if (parseInt(item.qtty) >= 1) {
        var temp = {};
        var price = item.price ? item.price : 0;
        price = +price;
        temp["category"] = item.category ? item.category : "Unorganized";
        temp["name"] = item.name;
        temp["description"] = item.desc;
        temp["price"] = price;
        temp["qty"] = item.qtty;
        var sub_total = parseInt(item.qtty) * price;
        temp["sub_total"] = parseFloat(sub_total).toFixed(2);
        Items.push(temp);
      }
    }
  });
  Order_details["Items"] = Items;
  var config = {
    method: "post",
    url: `${process.env.VUE_APP_API_ENDPOINT}fsdata/v1/createOrder/${country}/Phone/${phone}/OTP/${otp}`,
    data: Order_details,
  };
  await axios(config)
    .then(function(response) {
      Order_response = response.data;
    })
    .catch(function(error) {
      Order_response = error.response.data;
    });
  return Order_response;
};

export const urlShorter = (link) => {
  var result = "";
  var data = JSON.stringify({
    longDynamicLink: `${process.env.VUE_APP_SHORT_LINK_DOMAIN}/?link=${link}`,
    suffix: {
      option: "SHORT",
    },
  });

  var config = {
    method: "post",
    url: `${process.env.VUE_APP_SHORT_URL}?key=${process.env.VUE_APP_apiKey}`,
    headers: {
      "Content-Type": "application/json",
    },
    data: data,
  };

  return axios(config)
    .then(function(response) {
      result = response.data.shortLink;
      return result;
    })
    .catch(function(error) {
      console.log(error);
      return error;
    });
};

