export const initApp = async () => {
  firebase.auth().onAuthStateChanged(
    function(user) {
      store.dispatch("setDialog", false);
      if (user) {
        store.dispatch("fetchUser", user);
        var displayName = user.displayName;
        var email = user.email;
        var emailVerified = user.emailVerified;
        var photoURL = user.photoURL;
        var uid = user.uid;
        var phoneNumber = user.phoneNumber;
        var providerData = user.providerData;
        user.getIdToken().then(function(accessToken) {
          var loginuser = {
            displayName: displayName,
            email: email,
            emailVerified: emailVerified,
            phoneNumber: phoneNumber,
            photoURL: photoURL,
            uid: uid,
            accessToken: accessToken,
            providerData: providerData,
          };

          store.dispatch("setOverlay", true);
          var routename = router.currentRoute.name;
          if (routename != "viewform") {
            if (user) {
              login(loginuser);
              store.dispatch("setOverlay", false);
            } else {
              store.dispatch("setOverlay", false);
            }
          }
        });
      } else {
        //controller.navigateInitial(null);
        store.dispatch("setOverlay", true);
        var routename = router.currentRoute.name;
        if (routename != "viewform") {
          if (user) {
            login(user);
            loadMaster(user.uid, function() {
              store.dispatch("setOverlay", false);
            });
          } else {
            store.dispatch("setOverlay", false);
          }
        }
        return null;
      }
    },
    function() {
      //console.log(error);
    }
  );
};

