export const saveImage = (id, name, image, callback) => {
  var storageRef = firebase.storage().ref();
  var fname = "/files/" + id + "/img" + name;
  var fref = storageRef.child(fname);
  var content = dataURLtoFile(image, name);

  try {
    fref
      .put(content)
      .then(function() {
        fref.getDownloadURL().then(function(newurl) {
          callback(null, newurl);
        });
      })
      .catch(function(error) {
        callback(error, null);
      });
  } catch (error) {
    callback(error, null);
  }
};
export const dataURLtoFile = (dataurl, filename) => {
  var arr = dataurl.split(","),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], filename, { type: mime });
};

