export const updateMasterData = (state) => {
  if (!state.currentForm) return;
  const masterData = [...state.forms];
  const currentPossition = state.currentFormPos;
  let findCurrentForm = masterData.find((ele) => {
    return ele.link.split("/")[3] == currentPossition;
  });

  if (findCurrentForm) {
    const findCurrentFormIndex = masterData.findIndex((ele) => {
      return ele.link == findCurrentForm.link;
    });
    findCurrentForm.type = state.currentForm.type;
    findCurrentForm.shopname = state.currentForm.shopname;
    findCurrentForm.shopdesc = state.currentForm.shopdesc;
    masterData[findCurrentFormIndex] = findCurrentForm;
  } else {
    state.forms.push({
      shopname: state.currentForm.shopname,
      shopdesc: state.currentForm.shopdesc,
      type: state.currentForm.type,
      link: "/admin/editform/" + state.currentFormPos,
    });
  }
};
