export const generateJWTToken = async (id) => {
  var config = {
    method: "get",
    url: `${process.env.VUE_APP_API_ENDPOINT}fsdata/v1/create-jwt-token/${id}`,
    headers: {
      "Content-Type": "application/json",
    },
  };
  await axios(config)
    .then(async function(response) {
      await store.dispatch("setJWTToken", response.data.token);
    })
    .catch(function(error) {
      console.log(error);
    });
};
const userChecking = async (merchant_id, token) => {
  var config = {
    method: "get",
    url: `${process.env.VUE_APP_API_ENDPOINT}fsdata/v1/userChecking/${merchant_id}?token=${token}`,
    headers: {
      "Content-Type": "application/json",
    },
  };

  await axios(config)
    .then(function() {
      generateJWTToken(merchant_id);
    })
    .catch(function(error) {
      console.log(error);
    });
};

firebase.auth().onAuthStateChanged(
  function(user) {
    if (user) {
      Token(user.uid);
      saveLocale(user.uid, store.state.locale, function() {});
      store.state.currentForm.id = user.uid;
      if (user.metadata.lastSignInTime == user.metadata.creationTime) {
        userAnalytics("newUser", user);
        saveUser(user.uid, user, function(error, response) {
          if (error) {
            // alert(i18n.t("alertText.userDetails"));
          } else console.log(response);
        });
      } else {
        if (!store.state.profile.isSigned) {
          userAnalytics("returnUser", user);
          saveUser(user.uid, user, function(error, response) {
            if (error) {
              // alert(i18n.t("alertText.userDetails"));
            } else console.log(response);
          });
        }
      }
    }
    store.dispatch("setDialog", false);
    if (user) {
      store.dispatch("fetchUser", user);
      var displayName = user.displayName;
      var email = user.email;
      var emailVerified = user.emailVerified;
      var photoURL = user.photoURL;
      var uid = user.uid;
      var phoneNumber = user.phoneNumber;
      var providerData = user.providerData;
      user.getIdToken().then(function(accessToken) {
        var loginuser = {
          displayName: displayName,
          email: email,
          emailVerified: emailVerified,
          phoneNumber: phoneNumber,
          photoURL: photoURL,
          uid: uid,
          accessToken: accessToken,
          providerData: providerData,
        };

        store.dispatch("setOverlay", true);

        var routename = router.currentRoute.name;
        if (routename === "viewform") {
          store.dispatch("setOverlay", false);
          //Nothing
        } else {
          if (user) {
            login(loginuser);
            store.dispatch("setOverlay", false);
          } else {
            store.dispatch("setOverlay", false);
          }
        }
      });
    } else {
      store.dispatch("setOverlay", true);
      var routename = router.currentRoute.name;
      if (routename === "viewform") {
        //Nothing
      } else {
        if (user) {
          login(user);
          loadMaster(user.uid, function() {
            store.dispatch("setOverlay", false);
          });
        } else {
          store.dispatch("setOverlay", false);
        }
      }

      return null;
    }
  },
  function() {}
);
window.onscroll = () => {
  let scrollValue = window.scrollY;
  if (scrollValue > 60) {
    if (document.querySelector(".navbar")) {
      document.querySelector(".navbar").classList.add("affix");
    }
  } else {
    if (document.querySelector(".navbar")) {
      document.querySelector(".navbar").classList.remove("affix");
    }
  }
};
// onMessageListener
export const onMessageListener = () =>
  new Promise((resolve) => {
    if (firebase.messaging.isSupported()) {
      messaging.onMessage((payload) => {
        resolve(payload);
      });
    }
  });

export { messaging };
